<template>
  <div>
    <el-card>
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
      <el-table :data="orderlist" border stripe>
        <el-table-column type="index" align="center"></el-table-column>
        <el-table-column prop="order_number" label="" align="center"></el-table-column>
        <el-table-column prop="order_price" label="" align="center"></el-table-column>
        <el-table-column prop="pay_status" label="" align="center">
          <template slot-scope="scope">
            <el-tag type="success" v-if="scope.row.pay_status === '1'"></el-tag>
            <el-tag type="danger" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="update_time" label="" align="center">
          <template slot-scope="scope">{{scope.row.update_time*1000 |dateformat}}</template>
        </el-table-column>
        <el-table-column label="" align="center">
          <template>
            <el-tooltip class="item" content="" placement="top" :enterable="false">
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                circle
                style="margin:0 15px"
                @click="editdialogVisible =true"
              ></el-button>
            </el-tooltip>
            <el-tooltip class="item" content="" placement="top" :enterable="false">
              <el-button
                type="success"
                icon="el-icon-location"
                size="mini"
                circle
                style="margin:0 15px"
                @click="showprogress"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryinfo.pagenum"
        :page-sizes="[5,10,20]"
        :page-size="queryinfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="editdialogVisible" @close="close">
      <el-form :model="editorderForm" :rules="editRules" ref="editorderFormRef" label-width="100px">
        <el-form-item label="/" prop="address1">
          <el-cascader
            :options="cityData"
            v-model="editorderForm.address1"
            style="width:100%"
            :props="cascaderProps"
          ></el-cascader>
        </el-form-item>
        <el-form-item label="" prop="address2">
          <el-input v-model="editorderForm.address2"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editdialogVisible = false"> </el-button>
        <el-button type="primary" @click="editdialogVisible = false"> </el-button>
      </span>
    </el-dialog>
    <el-dialog title="" :visible.sync="progressdialogVisible">
      <el-timeline>
        <el-timeline-item
          v-for="(activity, index) in progressinfo"
          :key="index"
          :timestamp="activity.time"
        >{{activity.context}}</el-timeline-item>
      </el-timeline>
    </el-dialog>
  </div>
</template>

<script>
import { getorders_api, getlogistics_api } from "../../api/orders_api";
import cityData from "./citydata";
export default {
  data() {
    return {
      // 
      queryinfo: {
        query: "",
        pagenum: 1,
        pagesize: 10,
      },
      // 
      orderlist: [],
      // 
      total: 0,
      // 
      editdialogVisible: false,
      // 
      progressdialogVisible: false,
      // 
      editorderForm: {
        address1: [],
        address2: "",
      },
      // 
      progressinfo: [],
      editRules: {
        address2: [
          { required: true, message: "", trigger: "blur" },
        ],
      },
      cityData,
      cascaderProps: {
        expandTrigger: "hover",
        children: "children",
      },
    };
  },
  created() {
    this.getorderlist();
  },
  methods: {
    getorderlist() {
      getorders_api(this.queryinfo).then((res) => {
        console.log(res.data);
        if (res.data.meta.status !== 200) {
          return this.$message.error("!");
        }
        this.orderlist = res.data.data.goods;
        this.total = res.data.data.total;
      });
    },
    handleSizeChange(newpagesize) {
      this.queryinfo.pagesize = newpagesize;
      this.getorderlist();
    },
    handleCurrentChange(newpagenum) {
      this.queryinfo.pagenum = newpagenum;
      this.getorderlist();
    },
    close() {
      this.$refs.editorderFormRef.resetFields();
    },
    showprogress() {
      getlogistics_api().then((res) => {
        if (res.data.meta.status !== 200) {
          return this.$message.error("!");
        }
        this.progressinfo = res.data.data;
        console.log(this.progressinfo);
        this.progressdialogVisible = true;
      });
    },
  },
};
</script>

